import React, { useEffect } from "react";
import "./App.css";
import { ThemeProvider } from "./contexts/ThemeContext";
import { Header } from "./components/Header";
import { HeroSection } from "./components/HeroSection";
import { AboutSection } from "./components/AboutSection";
import { ServicesSection } from "./components/ServicesSection";
import { SkillsSection } from "./components/SkillsSection";
import { ContactSection } from "./components/ContactSection";
import { Footer } from "./components/Footer";

function App() {
  useEffect(() => {
    // Konami Code Easter Egg
    let konamiCode = [38, 38, 40, 40, 37, 39, 37, 39, 66, 65];
    let userInput = [];

    const handleKeyDown = (e) => {
      userInput.push(e.keyCode);
      
      if (userInput.length > konamiCode.length) {
        userInput.shift();
      }
      
      if (userInput.length === konamiCode.length) {
        let match = true;
        for (let i = 0; i < konamiCode.length; i++) {
          if (userInput[i] !== konamiCode[i]) {
            match = false;
            break;
          }
        }
        
        if (match) {
          // Easter egg activated!
          document.body.style.animation = 'rainbow 2s infinite';
          setTimeout(() => {
            document.body.style.animation = '';
            alert(' Konami Code !   +100  ! ');
          }, 2000);
          userInput = [];
        }
      }
    };

    document.addEventListener('keydown', handleKeyDown);

    // Console easter eggs
    console.log('%c CRYTEAM Portfolio v2.0.25', 'color: #ff6b6b; font-size: 20px; font-weight: bold;');
    console.log('%c     !', 'color: #4ecdc4; font-size: 14px;');
    console.log('%c  : showEasterEggs()', 'color: #45b7d1; font-size: 12px;');
    console.log('%c  : activateDevMode(), spawnRandomFact(), showStats()', 'color: #f39c12; font-size: 10px;');
    console.log('%c Hint:   Ctrl+Shift+C   !', 'color: #e74c3c; font-size: 10px;');
    
    // Add console functions
    window.showEasterEggs = () => {
      console.log('%c  :', 'color: #f39c12; font-size: 16px; font-weight: bold;');
      console.log('1.  Konami Code (BA)');
      console.log('2.  Skill tree  EXP ');
      console.log('3.     ');
      console.log('4.     ');
      console.log('5.       ');
      console.log('6.    "Caffeine Tolerance"');
      console.log('7.    ');
      console.log('8.  10-   ');
      console.log('%c   ...  ! ', 'color: #e74c3c; font-style: italic;');
    };

    window.activateDevMode = () => {
      document.documentElement.style.setProperty('--primary', '120 100% 50%');
      console.log('%c Dev Mode !      Matrix!', 'color: #00ff00; font-size: 16px;');
    };

    window.spawnRandomFact = () => {
      const facts = [
        " Minecraft    6 !",
        "     3    ",
        "      ",
        "  1981    : '640KB   '",
        "  -   : info.cern.ch",
        "   'Hello World'      15 ",
        "  Apollo 11    ",
        " 75%   Stack Overflow  "
      ];
      const randomFact = facts[Math.floor(Math.random() * facts.length)];
      console.log(`%c Random Dev Fact: ${randomFact}`, 'color: #9b59b6; font-size: 14px;');
    };

    window.showStats = () => {
      console.log('%c  :', 'color: #3498db; font-size: 16px; font-weight: bold;');
      console.log('  :  !');
      console.log('    : 42 ');
      console.log('  : 404 (not found)');
      console.log('  : Over 9000!');
      console.log('%c    50%  !', 'color: #f1c40f; font-size: 12px;');
    };

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, []);

  return (
    <ThemeProvider>
      <div className="App min-h-screen bg-background text-foreground">
        <Header />
        <main>
          <HeroSection />
          <AboutSection />
          <ServicesSection />
          <SkillsSection />
          <ContactSection />
        </main>
        <Footer />
      </div>
    </ThemeProvider>
  );
}

export default App;